.menu {
	display: flex;
	align-items: center;
	font-weight: 600;
	@include sm {
		flex-direction: column; }

	&__item {
		a {
			font-size: 16px;
			color: #ffffff;
			text-decoration: none;
			margin-right: 30px;
			@include md {
				font-size: 14px;
				margin-right: 20px; }
			@include sm {
				margin-right: 0;
				font-size: 13px; } }
		@include sm:hover {
			text-decoration: underline; }
		@include sm {
			padding: 10px 0; } }
	@include sm:last-child {
			padding-bottom: 0; } }


