html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

body {
  background: #FFF;
  line-height: 1.4;
}

body {
  font-family: 'Montserrat', sans-serif;
}

.block {
  display: flex;
}

.wrapper {
  width: 1170px;
  min-width: 320px;
  margin: 0 auto;
}

.header__inner {
  max-width: 990px;
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 1023px) {
  .header__inner {
    padding: 0 25px;
  }
}

@media only screen and (max-width: 767px) {
  .header__inner {
    padding: 20px 15px 0 15px;
  }
}

.bg-blue {
  background-color: #00bdca;
}

.header-wrapper {
  padding: 66px 0;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: flex-end;
}

@media only screen and (max-width: 767px) {
  .header-wrapper {
    display: none;
    padding: 0px;
  }
}

.logo {
  display: flex;
}

.logo__img {
  width: 43px;
  height: 44px;
}

@media only screen and (max-width: 1023px) {
  .logo__img {
    width: 33px;
    height: 34px;
  }
}

.logo__text {
  margin-left: 12px;
}

.logo__text p {
  color: #fff;
  font-weight: 600;
  font-size: 24px;
}

@media only screen and (max-width: 1023px) {
  .logo__text p {
    font-size: 18px;
  }
}

.logo__text span {
  color: #56ecf6;
}

.logo__text p:nth-child(2) {
  font-size: 13px;
  font-weight: 400;
  line-height: 3px;
}

@media only screen and (max-width: 1023px) {
  .logo__text p:nth-child(2) {
