  }
}

.logo__text p {
  font-weight: 600;
}

.header-right {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .header-right {
    flex-direction: row-reverse;
    width: 100%;
    justify-content: space-around;
  }
}

.btn {
  background-color: #fff;
  color: #1d354e;
  padding: 12px 30px;
  font-size: 16px;
  border-radius: 16px;
  font-weight: 600;
}

@media only screen and (max-width: 1023px) {
  .btn {
    font-size: 14px;
    padding: 10px 22px;
  }
}

@media only screen and (max-width: 767px) {
  .btn {
    font-size: 12px;
    padding: 6px 18px;
  }
}

.lang {
  position: relative;
  color: #1d354e;
  border-radius: 21px;
  background-color: #ffffff;
  font-weight: 600;
  margin-left: 8px;
}

.lang:hover .lang-list {
  opacity: 1;
  visibility: visible;
}

.lang:hover .lang__current {
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}

@media only screen and (max-width: 1023px) {
  .lang {
    background: none;
  }
  .lang__current {
    color: #fff;
  }
  .lang:hover .lang__current {
    color: #1d354e;
  }
  .lang:hover {
    background: #fff;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
  }
}

.lang-btn {
  background-color: #fff;
  color: #1d354e;
  padding: 12px 30px;
  font-size: 16px;
  border-radius: 16px;
  font-weight: 600;
}

@media only screen and (max-width: 1023px) {
  .lang-btn {
    background: none;
    font-size: 14px;
    padding: 10px 22px;
  }
}

@media only screen and (max-width: 767px) {
  .lang-btn {
    font-size: 12px;
    padding: 6px 18px;
  }
}

.lang-list {
  position: absolute;
  top: 100%;
  width: 100%;
  left: 0px;
  opacity: 0;
  visibility: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: #ffffff;
  border-bottom-right-radius: 16px;
  border-bottom-left-radius: 16px;
}

.lang-list__item {
  color: #1d354e;
  padding: 12px 30px;
  font-size: 16px;
  font-weight: 600;
}

@media only screen and (max-width: 1023px) {
  .lang-list__item {
    font-size: 14px;
    padding: 10px 22px;
  }
}

@media only screen and (max-width: 767px) {
  .lang-list__item {
    font-size: 12px;
    padding: 6px 18px;
  }
}

.lang__link {
  text-decoration: none;
  color: #1d354e;
}

.lang__current:hover {
  text-decoration: underline;
}

.lang__link:hover {
  text-decoration: underline;
  cursor: pointer;
}

#burger-menu:checked ~ .header-wrapper {
  position: absolute;
  width: 100%;
  top: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: space-around;
  flex-direction: column-reverse;
  padding: 10px 0;
}

.hidden-menu {
  display: none;
}

.btn-menu {
  display: none;
  color: #fff;
  cursor: pointer;
  transition: linear 2s;
  z-index: 3;
  width: 25px;
}

@media only screen and (max-width: 767px) {
  .btn-menu {
    position: absolute;
    right: 15px;
    display: block;
  }
}

.btn-menu span {
  display: block;
  height: 3px;
  background-color: #fff;
  margin: 5px 0 0;
  transition: all .1s linear 0.2s;
  position: relative;
  transition: 0.5s;
}

.hidden-menu:checked ~ .btn-menu span.first {
  transform: rotate(45deg);
  top: 10px;
  transition: 0.5s;
}

.hidden-menu:checked ~ .btn-menu span.second {
  opacity: 0;
}

.hidden-menu:checked ~ .btn-menu span.third {
  transform: rotate(-45deg);
  top: -6px;
  transition: 0.5s;
}

.menu {
  display: flex;
  align-items: center;
  font-weight: 600;
}

@media only screen and (max-width: 767px) {
  .menu {
    flex-direction: column;
  }
}

.menu__item a {
  font-size: 16px;
  color: #ffffff;
  text-decoration: none;
  margin-right: 30px;
}

@media only screen and (max-width: 1023px) {
  .menu__item a {
    font-size: 14px;
    margin-right: 20px;
  }
}

@media only screen and (max-width: 767px) {
  .menu__item a {
    margin-right: 0;
    font-size: 13px;
  }
}

.menu__item :hover {
  text-decoration: underline;
}

@media only screen and (max-width: 767px) {
  .menu__item {
    padding: 10px 0;
  }
}

.menu :last-child {
  padding-bottom: 0;
}

.preview-wrapper {
  max-width: 990px;
  display: flex;
  justify-content: flex-end;
}

.preview {
  max-width: 600px;
  text-align: center;
  color: #ffffff;
  padding: 60px 0;
}

@media only screen and (max-width: 1259px) {
  .preview {
